.root{color:var(--PSPDFKit-Error-color);height:100%;align-items:center;justify-content:center;font-family:-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Open Sans,Helvetica Neue,sans-serif}.box,.root{display:flex}.box{max-width:670px;background:var(--PSPDFKit-Error-background);box-shadow:0 3px 3px var(--PSPDFKit-shadow-color);border-radius:6px;align-items:stretch}.box a{color:var(--PSPDFKit-Error-hyperlink-color);text-decoration:underline}.box span{line-height:30px}.error{background-color:var(--PSPDFKit-Error-error-background);border-top-left-radius:6px;border-bottom-left-radius:6px}.error,.errorMessage{padding:15px 20px;display:flex;align-items:center}.icon{width:24px;height:24px;fill:var(--PSPDFKit-Error-icon-color);border-radius:12px;background-color:var(--PSPDFKit-Error-icon-background)}