.sidebar{position:relative;word-break:break-word;overflow-wrap:break-word;background-color:var(--PSPDFKit-Sidebar-background);height:100%}.dragger{cursor:col-resize;position:absolute;top:0;bottom:0;user-select:none;overflow:hidden;background-color:var(--PSPDFKit-Sidebar-dragger-background)}.dragger:after,.dragger:before{content:"";width:1px;background-color:var(--PSPDFKit-Sidebar-dragger-pseudo-background);height:20px;margin-top:-15px;position:absolute;top:50%;left:50%}.dragger:before{margin-left:-1px}.dragger:after{margin-left:1px}