.root{width:90%}@media (min-width:320px){.root{width:300px}}.progress,.root>button,.root>div{margin:.5em 0}.progress{position:relative;z-index:1;display:block;width:100%;padding:.25em .5em;border-radius:3px;background-color:var(--PSPDFKit-PrintLoadingIndicator-progress-background);color:var(--PSPDFKit-PrintLoadingIndicator-progress-color);overflow:hidden}.progressBar{position:absolute;top:0;bottom:0;left:0;background-color:var(--PSPDFKit-PrintLoadingIndicator-progressBar-background);z-index:-1}.progressText{color:var(--PSPDFKit-PrintLoadingIndicator-progressText-color)}