.root{flex-wrap:wrap;align-items:center;font-size:18px}.input,.root{display:flex;line-height:0}.input{cursor:pointer;padding:4px;border-radius:100%;transition:all .15s;touch-action:none;margin:4px 3px}.input input{display:none}.input:active,.input:focus,.input:hover{background:var(--PSPDFKit-ColorPicker-isHovered-background)}.swatch{display:inline-block;width:1em;height:1em;border-radius:100%;border:2px solid var(--PSPDFKit-ColorPicker-swatch-border);position:relative;z-index:2}.inputIsActive{background:var(--PSPDFKit-ColorPicker-input-isActive-background)!important}.swatchIsTransparent,.swatchIsWhite{border:2px solid var(--PSPDFKit-ColorPicker-swatch--transparent-border)}.swatchIsTransparent{background:var(--PSPDFKit-ColorPicker-swatch--transparent-background)!important}.swatchIsTransparent:after{content:"";display:inline-block;width:12px;border-bottom:1px solid var(--PSPDFKit-ColorPicker-swatch--transparent-after-border);transform:rotate(-60deg) translate(-2px,3px)}