.docEditor{position:relative;display:flex;flex-direction:column;width:100vw;height:100vh}@media (min-width:768px){.docEditor{width:calc(100vw - 64px);height:calc(100vh - 64px);border-radius:6px;overflow:hidden;isolation:isolate}}.pagesView{position:relative;background:var(--PSPDFKit-DocumentEditor-pagesView-background);flex:1;overflow:auto}.pagesTitle{display:flex;align-items:center;font-weight:700;font-size:18px}.tileContainer{position:relative}.newPage{width:180px;height:233px}.importedDocument{position:relative;display:flex;flex-direction:column;justify-content:center;align-items:center;padding:24px}.importedDocumentIconCircle{width:64px;height:64px;padding:12px;background:#4636e3;border-radius:50%;color:#fff}.importedDocumentInfo{display:none;margin-top:24px;color:rgba(0,0,0,.8)}.importedDocument:after,.importedDocument:before{position:absolute;top:0;left:0;content:"";width:100%;background:transparent;box-shadow:0 1px 2px rgba(0,0,0,.25)}.importedDocument:before{height:calc(100% - 6px)}.importedDocument:after{height:calc(100% - 3px)}.pagesGridLargeThumbnails .importedDocumentIconCircle{width:80px;height:80px;padding:16px}.pagesGridLargeThumbnails .importedDocumentInfo{display:initial}.pageEntrySelected .page{box-shadow:0 0 0 4px #5e5ceb}.pageLabel{color:#fff;background:rgba(0,0,0,.3);border-radius:6px;padding:4px 24px;margin-top:16px;font-weight:700;text-shadow:0 2px 3px rgba(0,0,0,.5);transition:background 40ms linear}.pageEntrySelected .pageLabel{background:#5e5ceb}.pagesSelectedIndicator{display:none;align-items:center;opacity:0;height:31px;background-color:var(--PSPDFKit-DocumentEditor-pagesSelectedIndicator-background);color:#fff;text-shadow:0 1.5px 3px rgba(0,0,0,.3);border-radius:3px;font-size:14px;padding:0 12px 0 8px}@media (min-width:768px){.pagesSelectedIndicator{display:flex}}.pagesSelectedIndicatorShown{opacity:1}.pagesSelectedIcon{width:24px;height:24px;margin-right:4px}.bottomBar{position:relative;display:flex;align-items:center;flex-shrink:0;padding:0 24px;height:66px;background:var(--PSPDFKit-DocumentEditor-bottomBar-background)}.bottomBar>:not(:last-child){margin-right:16px}.spacer{flex:1}.toolbarContainer{display:flex;overflow-x:hidden;overflow-y:auto;justify-content:flex-start;width:100%}