.layer{position:absolute;top:0;left:0;right:0;bottom:0;pointer-events:none}.annotation{position:absolute;pointer-events:auto;border:0;padding:0;background:transparent;line-height:1}.annotation:focus,.annotation:hover{cursor:pointer}.annotation[disabled]{cursor:auto}.icon{fill:currentColor;max-width:100%;stroke:#777}.wrapper{position:relative;display:flex;flex-direction:column;padding:10px;line-height:1.2em;overflow-y:auto;font-family:sans-serif;cursor:text;width:300px;height:200px;white-space:pre-wrap}.wrapper .content{margin-top:10px}.fullScreen{position:absolute;left:0;right:0;bottom:0;background:#fff;text-align:initial;margin:0;padding:15px 7px 15px 15px}.fullScreen .content{margin-top:15px}.title{margin:0;text-align:center;opacity:.5;text-shadow:0 1px 1px #fff;font-weight:400;font-size:14px}.content{background:transparent;position:relative;flex:1;overflow-y:auto;text-align:left}.tools{display:flex;justify-content:center;max-width:100%;overflow-y:auto;-webkit-overflow-scrolling:touch;-ms-overflow-style:none}.tool{opacity:.7}.tool:active,.tool:focus,.tool:hover,.tool[data-focus-within]{opacity:1}.tool:focus-within{opacity:1}