.root{display:flex;align-items:center;font-size:inherit;color:inherit;font-family:-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Open Sans,Helvetica Neue,sans-serif;margin:5px;line-height:32px}.label{padding-right:6px}.input{position:relative;display:flex;align-items:center;width:140px}.input input[type=range]{appearance:none;background:transparent;width:100%;min-width:70px;padding:0}.input input[type=range]::-webkit-slider-runnable-track{width:100%;min-width:70px;height:8px;cursor:pointer;background:#4636e3;border-radius:4px;border:0 solid #000}.input input[type=range]::-webkit-slider-thumb{box-sizing:border-box;border:2px solid var(--PSPDFKit-SliderComponent-thumb-border);height:16px;width:16px;border-radius:100%;background:var(--PSPDFKit-SliderComponent-thumb-color);cursor:pointer;box-shadow:0 1px 2px 1px rgba(0,0,0,.2);transition:background-color .2s;appearance:none;margin-top:-4px}.input input[type=range]::-webkit-slider-thumb:active,.input input[type=range]::-webkit-slider-thumb:focus,.input input[type=range]::-webkit-slider-thumb:hover{background:var(--PSPDFKit-SliderComponent-thumb-isHovered-color)}.input input[type=range]:focus::-webkit-slider-runnable-track{background:#4636e3}.input input[type=range]::-moz-range-track{width:100%;min-width:70px;height:8px;cursor:pointer;background:#4636e3;border-radius:4px;border:0 solid #000}.input input[type=range]::-moz-range-progress{width:100%;min-width:70px;height:8px;cursor:pointer;background:#4636e3;border-radius:4px;border:0 solid var(--PSPDFKit-SliderComponent-track-progress-color)}.input input[type=range]::-moz-range-thumb{box-sizing:border-box;border:2px solid var(--PSPDFKit-SliderComponent-thumb-border);height:16px;width:16px;border-radius:100%;background:var(--PSPDFKit-SliderComponent-thumb-color);cursor:pointer;box-shadow:0 1px 2px 1px rgba(0,0,0,.2);transition:background-color .2s}.input input[type=range]::-moz-range-thumb:active,.input input[type=range]::-moz-range-thumb:focus,.input input[type=range]::-moz-range-thumb:hover{background:var(--PSPDFKit-SliderComponent-thumb-isHovered-color)}.input input[type=range]::-moz-focus-outer{border:0}.input input[type=range]::-ms-track{width:100%;min-width:70px;height:8px;cursor:pointer;background:transparent;border-color:transparent;border-width:16px 0;color:transparent}:global(.PSPDFKit-browser-engine-trident) .input input[type=range]::-ms-track{margin-top:-2px;margin-bottom:-2px}:global(.PSPDFKit-browser-engine-edge) .input input[type=range]::-ms-track{margin-top:-8px;margin-bottom:-8px}.input input[type=range]::-ms-fill-lower{background:#4636e3;border:0 solid #4636e3;border-radius:8px}.input input[type=range]::-ms-fill-upper{background:#eee;border:0 solid #eee;border-radius:8px}.input input[type=range]::-ms-thumb{box-sizing:border-box;border:2px solid var(--PSPDFKit-SliderComponent-thumb-border);height:16px;width:16px;border-radius:100%;background:var(--PSPDFKit-SliderComponent-thumb-color);cursor:pointer;box-shadow:0 1px 2px 1px rgba(0,0,0,.2);transition:background-color .2s;background:#fff;transform:translateY(2px)}.input input[type=range]::-ms-thumb:active,.input input[type=range]::-ms-thumb:focus,.input input[type=range]::-ms-thumb:hover{background:var(--PSPDFKit-SliderComponent-thumb-isHovered-color)}:global(.PSPDFKit-browser-engine-trident) .input input[type=range]::-ms-thumb{transform:none}.value{text-align:right;white-space:nowrap;margin-left:8px}