.dialog{background:var(--PSPDFKit-Modal-dialog-backgroundColor);border-radius:6px;box-shadow:0 12px 32px 0 var(--PSPDFKit-Modal-dialog-boxShadowColor);color:var(--PSPDFKit-Modal-dialog-color);font-family:-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Open Sans,Helvetica Neue,sans-serif;font-size:13px;padding:var(--PSPDFKit-spacing-3);max-height:100vh;overflow-y:auto;isolation:isolate}.squareCorners{border-radius:0}.backdropContainer{position:absolute;left:0;top:0;right:0;bottom:0;display:flex;align-items:center;justify-content:center;touch-action:none;background-color:var(--PSPDFKit-Modal-backdrop-backgroundColor)}.section{margin-left:calc(-1*var(--PSPDFKit-spacing-3));margin-right:calc(-1*var(--PSPDFKit-spacing-3));padding:var(--PSPDFKit-spacing-3)}.section:first-child{margin-top:calc(-1*var(--PSPDFKit-spacing-3));border-radius:6px 6px 0 0}.section:last-child{margin-bottom:calc(-1*var(--PSPDFKit-spacing-3));border-radius:0 0 6px 6px}.divider{border-top:1px solid var(--PSPDFKit-Modal-divider-border);margin-left:calc(-1*var(--PSPDFKit-spacing-3));margin-right:calc(-1*var(--PSPDFKit-spacing-3))}.dividerSpaced,.sectionSpaced{margin-top:var(--PSPDFKit-spacing-3);margin-bottom:var(--PSPDFKit-spacing-3)}.shaking{animation:shake .82s cubic-bezier(.36,.07,.19,.97) both;transform:translateZ(0)}@keyframes shake{10%,90%{transform:translate3d(-1px,0,0)}20%,80%{transform:translate3d(2px,0,0)}30%,50%,70%{transform:translate3d(-4px,0,0)}40%,60%{transform:translate3d(4px,0,0)}}